.main-content {
  max-width: 630px;
  margin: -3% auto 90px;
  transform: scale(.8);

  button.red {
    background: #e85d54 !important;
    color: #fff;
  }
  button.orange {
    background: #ff8e18 !important;
    color: #fff;
  }

  .loading-spiny {
    > span {
      display: inline-block;
      width: 50px;
      height: 50px;
      background: url(../images/loading-2.png) 0 0 no-repeat;
      background-size: 100%;
      animation: sunSwapRotate 1s infinite linear;

      > svg {
        display: none;
      }
    }
  }

  .token-selector {
    display: flex;
    justify-content: flex-end;
  }
  #swap-tab {
    .ant-tabs-content-holder {
      padding: 86px 40px 24px;
      background: #2F2F41;
      border-radius: 30px
    }

    .ant-tabs-nav:before {
      border-bottom: none;
    }

    .ant-tabs-nav-list {
      width: 100%;

      .ant-tabs-tab {
        width: 50%;
        margin: 0;
        justify-content: center;
        opacity: 0.5;
        font-family: AvenirNext-DemiBold;
        font-size: 22px;
        color: #ffffff;

        &.ant-tabs-tab-active {
          opacity: 1;

          .ant-tabs-tab-btn {
            color: #fff;
            font-weight: 600;
          }
        }

        &:hover {
          opacity: 1;
        }
      }
    }

    .ant-tabs-ink-bar {
      display: none;
    }

    .ant-pagination {
      text-align: center;
      background: transparent;
    }

    button {
      border-radius: 20px;
      cursor: pointer;
      width: 100%;
      height: 70px;
      line-height: 70px;
      padding: 0;

      &.schedule-btn {
        margin-right: 33px;
        width: 100px;
        height: 40px;
        min-width: auto;
        line-height: 40px;
        border-radius: 10px;
        background: rgba($color: #4850e6, $alpha: 0.2);
        color: #4850e6;
        font-size: 16px;
        font-family: AvenirNext-Medium;

        &:last-child {
          margin-right: 0;
        }

        &:hover {
          background-color: #bdc0f5;
        }
      }

      &.schedule-btn-active {
        background: $blue;
        height: 40px;
        line-height: 40px;
        color: #fff;
      }

      font-family: AvenirNext-Medium;
      font-size: 16px;

      &.half {
        width: 49%;
        max-width: 260px;
        margin-left: 2%;

        &:first-child {
          margin-left: 0;
        }
      }

      &.trisection {
        width: 32%;
        max-width: 160px;
        margin-left: 25px;

        &:first-child {
          margin-left: 0;
        }
      }

      &.gray {
        cursor: auto;
        color: #fff;
        background: nth($gray, 2);
      }

      &.blue {
        background: $blue;
        color: #fff !important;
        position: relative;

        &:hover {
          background: $hoverBlue;
        }

        .ques {
          position: absolute;
          top: 26px;
          right: 26px;
        }
      }

      &.purple {
        background: rgba($color: $blue, $alpha: 0.2);
        color: $blue;
      }

      &.green {
        background: rgba($color: $green, $alpha: 0.2);
        color: $green;
      }

      &.ant-pagination-item-link {
        height: 32px;
        line-height: 32px;
      }
    }

    ::placeholder {
      font-family: AvenirNext-DemiBold;
      font-size: 20px;
      color: #999999;
    }
  }
  .total-ele {
    width: 150%;
    text-align: center;
    margin-left: -25%;
    padding: 30px 0 40px;
    margin-bottom: 35px;
    // background-color: rgba(255, 255, 255, 0.1);
    // border-radius: 40px;
    font-family: Avenir Next;

    > p {
      color: #fff;
      text-align: center;
    }

    .total-title {
      font-size: 18px;
      line-height: 25px;
      margin-bottom: 20px !important;
    }

    .total-value {
      font-weight: bold;
      font-size: 50px;
      line-height: 50px;
    }

    .custom-count {
      white-space: nowrap;
    }
  }

  @media screen and (max-width: 500px) {
    .total-ele {
      margin-bottom: 0;
      padding-bottom: 30px;

      .total-title {
        font-size: 16px;
      }

      .total-value {
        font-size: 35px;
      }
    }
  }


  /*  */
  .ques {
    color: nth($black, 3);
    display: inline-block;
    background: #fff;
    min-width: 18px;
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    border-radius: 100%;
    // margin-left: 10px;
    margin-right: 10px;
    border: 1px solid nth($black, 3);
    font-family: AvenirNext-Medium;
    font-size: 16px;
    cursor: pointer;
  }

  /*  */
  .link {
    color: $blue;
    cursor: pointer;
    font-family: AvenirNext-Medium;
    font-size: 14px;

    &:hover {
      color: $hoverBlue;
    }
  }
}

.swap-and-pool {

  /* tooltip */
  .common-tool {
    margin: 20px 0;

    .ques {
      margin-right: 0 !important;
    }
  }

  .common-tool-title {
    font-family: AvenirNext-Medium;
    font-size: 14px;
    color: #fff;
  }
  .switch-version {
    .ant-tabs-content {
      position: relative;
      z-index: 2;
    }

    .ant-tabs-content-holder {
      position: relative;

      &::before {
        content: '';
        display: block;
        width: 90px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 3;
        border-radius: 30px 0 30px 0;
        font-family: Avenir Next;
        font-weight: 600;
        font-size: 15px;
        // background-position: 0 0;
        // background-repeat: no-repeat;
        // background-size: 100%;
      }

      &::after {
        content: '';
        display: block;
        width: 100%;
        height: 240px;
        position: absolute;
        bottom: 0;
        right: 5px;
        z-index: 1;
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: 100%;
        border-radius: 0 0 50px 0;
      }
    }

    &.v1 {
      .ant-tabs-content-holder {
        &::before {
          content: 'V1';
          font-size: 18px;
          color: #715fdf;
          background: rgba(113, 95, 223, 0.1);
        }

        &::after {
          border-radius: 0;
          right: 15px;
          background: url(../images/v1-bg.svg) right no-repeat;
          background-size: auto 240px;
        }
      }
    }

    &.v15 {
      .ant-tabs-content-holder {
        &::before {
          content: 'V1.5';
          color: #dc4bdf;
          font-size: 18px;
          background: rgba(220, 75, 223, 0.1);
        }

        &::after {
          // width: 480px;
          // height: 204px;
          background: url(../images/v15-bg.svg) right no-repeat;
          background-size: auto 240px;
        }
      }
    }

    &.v2 {
      .ant-tabs-content-holder {
        &::before {
          font-size: 18px;
          content: 'V2';
          color: #e44646;
          background: rgba(229, 135, 135, 0.1);
        }

        &::after {
          right: -1px;
          background: url(../images/v2-bg.svg) right no-repeat;
          background-size: auto 240px;
        }
      }
    }
  }
}

.sun-swap-modal {
  &.ant-zoom-sun-enter,
  &.ant-zoom-sun-appear {
    -webkit-animation-duration: 0.2s;
    animation-duration: 0.2s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-play-state: paused;
    animation-play-state: paused;
  }
  &.ant-zoom-sun-leave {
    -webkit-animation-duration: 0.2s;
    animation-duration: 0.2s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-play-state: paused;
    animation-play-state: paused;
  }
  &.ant-zoom-sun-enter.ant-zoom-sun-enter-active,
  &.ant-zoom-sun-appear.ant-zoom-sun-appear-active {
    -webkit-animation-name: antZoomInSun;
    animation-name: antZoomInSun;
    -webkit-animation-play-state: running;
    animation-play-state: running;
  }
  &.ant-zoom-sun-leave.ant-zoom-sun-leave-active {
    -webkit-animation-name: antZoomOutSun;
    animation-name: antZoomOutSun;
    -webkit-animation-play-state: running;
    animation-play-state: running;
    pointer-events: none;
  }
  &.ant-zoom-sun-enter,
  &.ant-zoom-sun-appear {
    transform: scale(0);
    opacity: 0;
    -webkit-animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
    animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
  }
  &.ant-zoom-sun-enter-prepare,
  &.ant-zoom-sun-appear-prepare {
    transform: none;
  }
  &.ant-zoom-sun-leave {
    -webkit-animation-timing-function: cubic-bezier(0.78, 0.14, 0.15, 0.86);
    animation-timing-function: cubic-bezier(0.78, 0.14, 0.15, 0.86);
  }

  @keyframes antZoomInSun {
    0% {
      transform: scale(0.2);
      opacity: 0;
    }

    100% {
      transform: scale(0.8);
      opacity: 1;
    }
  }

  @keyframes antZoomOutSun {
    0% {
      transform: scale(0.8);
    }

    100% {
      transform: scale(0.2);
      opacity: 0;
    }
  }
}

